@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700&display=swap);
html, body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

div#root {
  height: 100vh;
  max-width: 1024px;
  margin: 0 auto;
}

@media (min-width: 320px) and (max-width: 720px) {
  div#root {
    width: 100vw;
  }
}

p {
  margin: 0;
}

span.highlighted {
  color: #4885ed;
}

div#app-wrapper {
  display: flex;
  height: 100vh;
}

div#main-content {
  display: flex;
  flex-direction: column;
  width: 70%;
  height: 100vh;
  padding: 16px;
}

div#sidebar {
  display: flex;
  flex-direction: column;
  width: 30%;
  margin: 16px 0;
  border: 2px solid #4885ed;
  border-radius: 8px;
}

div.sentence-translated {
  margin: 32px 0;
}

.translation {
  color: #c00000;
}

img.squared-img {
  margin: 0 auto;
  height: 200px;
  width: 200px;
}

@media (min-width: 320px) and (max-width: 720px) {
  div#app-wrapper {
    flex-direction: column-reverse;
  }

  div#main-content {
    width: 100vw;
  }

  div#sidebar {
    width: 100vw;
    border: none;
  }

  img.squared-img {
    margin: 0 auto;
    height: 120px;
    width: auto;
  }
}
div#starting-page {
  display: flex;
  flex-direction: column;
  height: 80%;
  justify-content: center;
  text-align: center;
  margin: 0 auto;
}

h1.title {
  font-size: 3em;
}

h6.sub-title {
  text-align: center;
}
div#top-title {
  padding: 16px;
  border: 2px solid #4885ed;
  border-radius: 8px;
  text-align: center;
}

h2.topbar-title {
  font-size: 2em;
}

@media (min-width: 320px) and (max-width: 720px) {
  div#top-title {
    display: none;
  }
}
div#content-box {
  font-size: 2em;
  margin: 64px 0 0 0;
  flex-grow: 1;
  padding: 16px;
  border: 2px solid #4885ed;
  border-radius: 8px;
  text-align: center;
  background: rgb(202,229,251);
  background: linear-gradient(180deg, rgba(202,229,251,1) 17%, rgba(130,176,255,1) 100%);
}

div#subtitle-container {
  margin-top: -50px;
  padding: 8px 32px;
  border: 3px solid #ffffff;
  border-radius: 64px;
  background: #f79646;
  color: #ffffff;
}

div#content {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-around;
}

span.bold {
  font-weight: bold;
}

@media (min-width: 320px) and (max-width: 720px) {
  div#content-box {
    font-size: 1.5em;
    margin: 32px 0 0 0;
  }

  h3.content-subtitle {
    font-size: 20px;
  }
}

p.question-description {
  font-weight: bold;
}

div#game-status-sidebar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 200px;
  width: 200px;
  margin: 16px auto;
  font-size: 1.5em;
  font-weight: 700;
}

@media (min-width: 320px) and (max-width: 720px) {
  div#game-status-sidebar {
    display: block;
    height: auto;
    width: auto;
  }
}
div.message-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  height: 20vh;
  font-size: 1.5em;
  padding: 16px;
}
@media (min-width: 320px) and (max-width: 720px) {
  div#app-wrapper {
    display: flex;
    flex-direction: column-reverse;
  }

  div#sidebar {
    height: 10%;
  }

  div#main-content {
    height: 90%;
  }
}

div#level-selection-container {
  display: flex;
  flex-direction: column;
}

button {
  margin: 16px 0;
}
